import Vue from 'vue'
import App from './App.vue'
import sweetAlert from 'sweetalert'
import VeeValidate from 'vee-validate';

const config = {
  locale: 'tw',
  dictionary: {
    tw: {
      messages: {
        email: () => ' Email ',
        required: () => '',
        isGmail: () => ' Gmail '
      }
    }
  },
};
Vue.use(VeeValidate, config);

new Vue({
  el: '#app',
  render: h => h(App)
})


